<template>
  <div id="app">
    <div class="content2" v-show='isshow'>
        <div class="add">
            <div class="header">
                <span style="float: left;">新增案例库</span>
                <span style="float: right; cursor: pointer;"  @click='show'>X</span>
            </div>
            <div class="zhong" style="margin-top: 50px; margin-left: 100px; text-align: left;  width: 500px;">
                    <el-form :inline="true" :model="form" class="demo-form-inline">
                            <el-form-item  label="案例名称" >
                              <el-input style="width: 350px;" v-model="form.keyword" placeholder="案例名称" id="btn1"></el-input>
                            </el-form-item>
                 </el-form>
                 <el-form :inline="true" :model="form" class="demo-form-inline">
                        <el-form-item  label="上传附件：" >
                          <div>
                            <input type="file" class="file" id="btn0">

                          </div>
                        </el-form-item>
             </el-form>
            </div>
            <div class="bottom" style="text-align: center; margin-top: 30px;" >
                    <el-row>
                            <el-button type="primary" @click="update()">保存</el-button>
                    </el-row>
            </div>
        </div>
    </div>
    <div class='content'>
        <div class="center">
            <div class="img">
                <img src="../../../src/assets/img/img2.jpg" alt="" width="80px" height="80px">
                <el-row>
                        <el-button type="primary" @click='show' >新增案例库</el-button>
                </el-row>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
  export default {
    name: 'Addanli',
    data() {
      return {
        isshow:false,
        form:{
            keyword:''
        },
      }
    },
    methods: {
      show() {
        this.isshow = !this.isshow
      },
      update() {
        // var formData = new FormData() // 声明一个FormData对象
        var formData = new window.FormData(); // vue 中使用 window.FormData(),否则会报 'FormData isn't definded'
        alert(this.keyword);
        formData.append('file', document.getElementById('btn0').files[0]);
        formData.append('keyword', document.getElementById('btn1').value);
        var options = {  // 设置axios的参数
          url: 'http://localhost:8084/caseLibrary/add',
          data: formData,
          method: 'post',
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        };
        axios(options).then((res) => {
          console.log(res);
          alert('文件 is ok');
          this.$router.push({
            path: './Afteradd',
          })
        })

      },
      // dataPush(){
      //   var data = {
      //     keyword: this.form.keyword
      //   }
      //   // localStorage.setItem('data', JSON.stringify(data))
      //
      //   axios.post("http://localhost:8084//caseLibrary/add",{
      //     keyword:this.form.keyword,
      //   }).then(res=>{
      //     this.$router.push({
      //     path: './Afteradd',
      //   })
      //   })
      // }
      },
      activated() {
        this.$router.push(this.path);   //处理默认现实的路由
      },
      beforeRouteLeave(to, from, next) {
        this.path = this.$route.path;
        next();
      },
    }

</script>

<style lang="" scoped>
  .content {
    margin-top: 20px;
    position: absolute;
    width: 97%;
    height: 89%;
    left:1.5%;
    background: white;
  }
 html,body{
  width: 100%;
  height: 100%;
}
  #app{
    margin-top: 10px;
    width: 97%;
    height: 90%;
    margin-left: 1.5%;
    background: white;
    position: absolute;
    box-shadow: 0px 0px  1px black;
  }
  .center{
      position: absolute;
      width: 150px;
      height: 150px;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      /* border: 2px solid red; */
      text-align: center;
  }

  .el-button{
      padding: 3px;
      width: 80px;
      height:30px;
      /* text-align: center; */
      /* line-height: px; */
  }

  .content2{
      position: absolute;
      width: 100%;
      height: 100%;
      /* background: black; */
      background-color:rgba(0,0,0,0.5);
      z-index: 2;
      
  }

  .add{
    position: absolute;
      width: 650px;
      height: 260px;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      padding: 15px;
      background: white;
  }
</style>



